.about-us {
  @include pageGrid();

  &__title {
    grid-column: 1 / -1;

    @include onTablet() {
      grid-column: 1 / 4;
    }

    @include onDesktop() {
      grid-column: span 5;
    }
  }

  &__description {
    grid-column: 1 / -1;

    @include onTablet() {
      grid-column: 4 / 7;
    }

    @include onDesktop() {
      grid-column: span 6;
    }

    &--content {
      margin: 0;
      font-size: 23px;
      line-height: 140%;
    }
  }
}
